.nav {
    li {
        cursor: pointer;
    }
    position: relative;
    top: 0px;
    left: 0px;
    z-index: 10;
    box-sizing: border-box;
    min-width: 100vw;
    padding: 0px $p-lg;
    background-color: #f1f1f1;
    box-shadow: 0px 10px 10px rgb(243, 243, 243);
    transition: all 1s ease;
    @include createFlex(row, space-between, center, null);
    &__navbar {
        list-style-type: none;
        padding: $p-zero;
        margin: $m-zero;
        min-width: 15%;
        @include createFlex(row, space-between, center, null);
    }
    &__item {
        cursor: pointer;
        position: relative;
        top: 0px;
        left: 0px;
        margin-right: $m-sm;
        &:before {
            content: '';
            position: absolute;
            bottom: -7px;
            left: 0px;
            width: 0%;
            border-bottom: 2px solid $black;
            transition: 0.5s ease-in-out;
        }
        &:hover {
            &:before {
                width: 100%;
            }
        }
        &-menu {
            visibility: hidden;
            opacity: 0;
            width: 200px;
            position: absolute;
            top: 80px;
            left: 0px;
            list-style-type: none;
            background-color: #f1f1f1;
            box-shadow: 0px 10px 10px rgb(243, 243, 243);
            padding: 20px;
            margin: 0px;
            transition: all 1s ease-in-out;
            li {
                cursor: pointer;
                margin: 10px 0px;
                padding: 10px 20px;
                &:hover {
                    background-color: #c2c1c1;
                }
            }
            &.open {
                visibility: visible;
                top: 45px;
                opacity: 1;
            }
        }
    }
    &__title {
        margin-left: 100%;
        font-size: $font-nav;
    }
    &__menu {
        margin: 0px;
        padding: 0px;
        min-width: 35%;
        @include createFlex(row, flex-end, center, null);
        &-item {
            margin-left: 20px;
            @include createFlex(row, flex-start, center, null);
            svg {
                margin-right: 10px;
            }
        }
    }
    &__badge {
        background-color: $badge;
        padding: $p-xs $p-sm;
        margin: $m-xs;
        border-radius: 100%;
    }
}